import { useState } from "react";
import { pdfjs, Document, Page } from "react-pdf";
// import { PDFDownloadLink } from "@react-pdf/renderer";
import "react-pdf/dist/Page/AnnotationLayer.css";
import "react-pdf/dist/Page/TextLayer.css";
import CreateButton from "../Button/CreateButton";
import style from "../../UserStaffMember/FinanceManager/Income/Income.module.css";
import { cdnUrl } from "../../Services/UrlConfig/BaseUrl";
import Modal from "../Modal/Modal";
import UserMemberContentWrapper from "../UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
import BorderBottom from "../Border/BorderBottom";
import Loading from "../../Loader/Loading";
pdfjs.GlobalWorkerOptions.workerSrc = new URL(
  "pdfjs-dist/build/pdf.worker.min.mjs",
  import.meta.url
).toString();

const ShowingGeneratedPdf = ({ documentUrl, subUrl, fileName, onClose }) => {
  const { t } = useTranslation();
  const [numPages, setNumPages] = useState(0);
  const [pageNumber, setPageNumber] = useState(1);

  const onDocumentLoadSuccess = (numPages) => {
    setNumPages(numPages);
  };

  //   1723868730428-ald_acu-bank-daybook.pdf

  const onSaveFile = () => {};
  return (
    <Modal onClose={onSaveFile}>
      <div className={style.pdf_modal_container}>
        <UserMemberContentWrapper>
          <div className={style.modal_container_outer_header}>
            <div className={style.modal_container_header}>
              <h6>
                {fileName ?? ""}
                {/* - {t("pdf_preview")} */}
              </h6>
            </div>
            <div className={style.modal_container_header}>
              <a
                href={`${cdnUrl}/${documentUrl}`}
                target="_blank"
                rel="noopener noreferrer"
              >
                <CreateButton
                  label="save"
                  customStyle={{
                    margin: "0",
                    padding: "0.2rem 1rem",
                  }}
                  onAction={onSaveFile}
                />
              </a>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />

        <Document
          file={`${subUrl}/${documentUrl}`}
          onLoadSuccess={onDocumentLoadSuccess}
          loading={<Loading />}
        >
          <Page
            pageNumber={pageNumber}
            //  pageIndex={numPages}
          />
        </Document>
      </div>

      {/* <p>
        Page {pageNumber} of {numPages}
      </p> */}
    </Modal>
  );
};
export default ShowingGeneratedPdf;
